import Vue from 'vue';
import Component from 'vue-class-component'

@Component({
  components: {
  },
})

export default class DispatchManage extends Vue {
    // table
    columns = [
      {
        type: 'index',
        width: 70,
        title: "序号",
        align: 'center'
      },
      {
        title: "文件名",
        key: 'templateName',
        align: 'center'
      },
      {
        title: "上传时间",
        key: 'uploadTime',
        align: 'center',
        render: (h, params) => {
          return h(
            "div",
            this.dayjs(params.row.uploadTime).format("YYYY-MM-DD HH:mm:ss")
          )
        }
      },
      {
        title: '操作',
        slot: 'action',
        width: 400,
        align: 'center'
      },
    ];
    tabledata = [];
    selectionList = new Set();
    rowMap = {};
    uploadState = false
    searchValue = ''
    // table操作

    generation (index) {
      console.log('aaaaaaaaaaaaaaa', index)
      let srcUrl = this.tabledata[index].templateHtmlUrl
      var i = srcUrl.lastIndexOf("\/");
      let iframeUrl = srcUrl.substring(i+1,srcUrl.length);
      console.log('ssssssssssssssss',this.tabledata[index])
      let templateMsg = {
        templateId: this.tabledata[index].id,
        sourceId: this.tabledata[index].dbSourceId,
        htmlUrl: iframeUrl
      }
      this.$store.commit('saveTemplateMsg', templateMsg)
      this.$router.push('/templateChange')
    }
    remove (index) {
      this.$api.TEM_DELETE('/delete', {
        templateId: this.tabledata[index].id
      }).then(res => {
        console.log('删除成功', res)
        this.tabledata.splice(index, 1);
      })
      
    }

    upload() {
      this.uploadState = !this.uploadState
    }

    search() {
      this.$api.TemFindByName('/findByName', {
        templateName: this.searchValue
      }).then(res => {
        console.log('搜索列表：', res.data.body);
        this.tabledata = res.data.body
      })
    }

    before(val)  {
      console.log('shanghcuan', val)
    }

    uploadSuccess(response) {
      console.log('upload', response)
    }

    createWord(index) {
      
      this.$api.CREATE_WORD('/create_word', {
        data: {
          dbSource_id: this.tabledata[index].dbSourceId,
          templateId: this.tabledata[index].id
        },
        name: this.tabledata[index].templateName
      })
    }

    uploadSuccess(response) {
      console.log('response', response)
    }

    created() {
      this.$api.FIND_ALL('/findAll')
      .then(res => {
        console.log('模板列表：', res.data.body)
        this.tabledata = res.data.body
      })
    }
}


