<template>
  <div class="main-wrapper">
    <div class="bread">
      <span class="bread-name">当前位置&nbsp;：</span>
      <Breadcrumb separator=">">
        <BreadcrumbItem>模板管理</BreadcrumbItem>
      </Breadcrumb>
    </div>
    <div class="content-wrapper">
      <div class="content-header">
        <Button
          class="btn-top-menu"
          custom-icon="sjzl-icon sjzl-icon-add"
          type="text"
          @click="upload"
        >
          <span class="title">上传</span>
        </Button>
        <div class="search">
          <Input
            type="text"
            placeholder="根据模板名搜索"
            ref="searchinput"
            v-model="searchValue"
            @on-enter="search"
          />
          <Button
            type="default"
            shape="circle"
            class="search-circle"
            custom-icon="sjzl-icon sjzl-icon-search"
            @click="search"
          ></Button>
        </div>
        <div class="h-line"></div>
        <div class="content-table">
           <Table
            ref="selection"
            :columns="columns"
            stripe
            :data="tabledata"
            tooltip-theme = "light" 
          >
            <template slot-scope="{ row }" slot="name">
              <strong>{{ row.name }}</strong>
            </template>
            <template slot-scope="{ row, index }" slot="action">
              <Button type="text" ghost size="small" style="margin-right: 5px" @click="generation(index)">快速生成</Button>
              <Button v-if="row.dbSourceId" type="text" ghost size="small" style="margin-right: 5px" @click="createWord(index)">生成电文</Button>
              <Button type="text" ghost size="small" @click="remove(index)">删除</Button>
            </template>
          </Table>
        </div>
        <Modal v-model="uploadState" title="上传">
          <Upload
              multiple
              type="drag"
              accept='.zip'
              :format="['zip']"
              @before-upload="before"
              action="http://127.0.0.1:8081/template/fileOperate/upload_template_html">
              <div style="padding: 20px 0">
                  <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
                  <p>Click or drag files here to upload</p>
              </div>
          </Upload>
        </Modal>
      </div>
    </div>
  </div>
</template>
<script>
import DispatchManage from "./dispatchManage.class"
export default DispatchManage;
</script>
<style lang="scss" scoped>
@import "../index.scss";
.ivu-btn-ghost {
  color: #3972F2;
}
</style>